<template>
  <div class="notice-page">
    <div class="box">
      <b-button type="default" @click="open1"></b-button>
      <b-button type="info" @click="open2"></b-button>
      <b-button type="success" @click="open3"></b-button>
      <b-button type="warning" @click="open4"></b-button>
      <b-button type="error" @click="open5"></b-button>
    </div>
    <div class="box">
      <b-button type="info" @click="infoClick"></b-button>
      <b-button type="success" @click="successClick"></b-button>
      <b-button type="warning" @click="warningClick"></b-button>
      <b-button type="error" @click="errorClick"></b-button>
      <b-button type="joker" @click="close"></b-button>
    </div>
  </div>
</template>
<script>
export default {
  name: "NoticePage",
  methods: {
    close(){
      this.$Notice.noticeClose(()=>{
        console.log('-----');
      })
    },
    open1() {
      this.$Notice.open({
        title: "",
        desc:
          "",
      });
    },
    open2() {
      this.$Notice.open({
        icon: true,
        type: "info",
        title: "open",
        desc:
          "openopenopenopenopenopen",
      });
    },
    open3() {
      this.$Notice.open({
        icon: true,
        type: "success",
        title: "",
        desc: "",
      });
    },
    open4() {
      this.$Notice.open({
        icon: true,
        type: "warning",
        title: "",
        desc: "",
        duration: 8,
      });
    },
    open5() {
      this.$Notice.open({
        icon: true,
        type: "error",
        title: "",
        desc: "",
        duration: 2,
      });
    },
    infoClick() {
      this.$Notice.info({
        title: "info",
        desc: "info",
        duration: 5,
      });
    },
    successClick() {
      this.$Notice.success({
        title: "success",
        desc: "success",
        duration: 3,
      });
    },
    warningClick() {
      this.$Notice.warning({
        title: "warning",
        desc: "warning",
        duration: 5,
      });
    },
    errorClick() {
      this.$Notice.error({
        title: "error",
        desc: "error",
        duration: 5,
      });
    },
  },
};
</script>
<style scoped>
.notice-page .box button {
  margin-right: 20px;
  margin-top: 20px;
}
</style>