<template>
  <aside class="leftmenu">
    <ul>
      <router-link :to="item.link" v-for="(item, index) in list" :key="index">
        <li
          @click="handleClick(index)"
          :class="[currentIndex == index ? 'action' : '']"
        >
          {{ item.name }}
        </li>
      </router-link>
    </ul>
  </aside>
</template>
<script>
export default {
  name: "LeftMenu",
  data() {
    return {
      list: [
        { name: "button", link: "/button" },
        { name: "input", link: "/input" },
        { name: "switch", link: "/switch" },
        { name: "radio", link: "/radio" },
        { name: "checkbox", link: "/checkbox" },
        { name: "alert", link: "/alert" },
        { name: "modal", link: "/model" },
        { name: "toast", link: "/toast" },
        { name: "notice", link: "/notice" },
        { name: "tooltip", link: "/tooltip" },
        { name: "poptip", link: "/poptip" },
        { name: "progress", link: "/progress" },
        { name: "circle", link: "/circle" },
        { name: "swiper", link: "/swiper" },
        { name: "tag", link: "/tag" },
        { name: "avatar", link: "/avatar" },
        { name: "menu", link: "/menu" },
        { name: "loading", link: "/loading" },
        { name: "scroll", link: "/scroll" },
        { name: "list", link: "/list" },
        { name: "table", link: "/table" },
      ],
      currentIndex: 0,
    };
  },
  methods: {
    handleClick(index) {
      this.currentIndex = index;
    },
  },
};
</script>
<style scoped>
.leftmenu {
  width: 280px;
  height: calc(100vh - 59px);
  float: left;
  border-right: 1px solid var(--border);
  font-size: 17px;
  cursor: pointer;
  font-weight: bolder;
  overflow: auto;
}
.leftmenu ul {
  list-style-type: none;
}
.leftmenu ul li {
  height: 40px;
  line-height: 40px;
  padding: 0px 10px;
}
a {
  text-decoration: none;
  color: var(--text-bold);
}
.action {
  color: #4abc88;
}
</style>