// 
function now() {
  return +new Date()
}
/**
 *  waitfunc 
 *
 * @param  {function} func        
 * @param  {number}   wait        
 * @param  {boolean}  immediate    ture 
 * @return {function}             
 */
export function debounce(func, wait = 50, immediate = true) {
  let timer, context, args

  // 
  const later = () => setTimeout(() => {
    // 
    timer = null
    // 
    // 
    if (!immediate) {
      func.apply(context, args)
      context = args = null
    }
  }, wait)

  // 
  return function (...params) {
    // later
    if (!timer) {
      timer = later()
      // 
      // 
      if (immediate) {
        func.apply(this, params)
      } else {
        context = this
        args = params
      }
      // later
      // 
    } else {
      clearTimeout(timer)
      timer = later()
    }
  }
}

/** */
export function debounce2(fn, delay) {
  let timer = null;

  //
  return (...args) => {
    if (timer) clearTimeout(timer);
    timer = setTimeout(() => {
      fn && fn.apply(this, args)
    }, delay)
  }
}

// --
export function throttle(fn, wait) {
  var pre = Date.now();
  return function () {
    var context = this;
    var args = arguments;
    var now = Date.now();
    if (now - pre >= wait) {
      fn.apply(context, args);
      pre = Date.now();
    }
  }
}