<template>
  <div class="toast">
    <b-toast content="" ref="toast" top="200px" />
    <div class="box">
      <b-button type="primary" @click="primaryClick"></b-button>
      <b-button type="info" @click="infoClick"></b-button>
      <b-button type="success" @click="successClick"></b-button>
      <b-button type="warning" @click="warningClick"></b-button>
      <b-button type="error" @click="errorClick"></b-button>
    </div>
    <div class="box">
      <b-button type="primary" @click="timeToast">10s</b-button>
    </div>
  </div>
</template>
<script>
export default {
  name: "Toast",
  methods: {
    primaryClick() {
      this.$refs.toast.show();
    },
    infoClick() {
      this.$Toast.info("");
    },
    successClick() {
      this.$Toast.success("");
    },
    warningClick() {
      this.$Toast.warning("");
    },
    errorClick() {
      this.$Toast.error("");
    },
    timeToast(){
      this.$Toast.info("10s",10);  
    }
  },
};
</script>
<style scoped>
.box {
  padding: 100px 0px;
}
.box button {
  margin-right: 20px;
  margin-top: 20px;
}
</style>