export const table = {
    computed: {
        /** */
        
        /** */
        isSplit() {
            return this.vbestTableHead.split || this.vbestTable.split;
        },
        /** */
        getSplitColor() {
            return (
                this.vbestTableHead.splitColor ||
                this.vbestTable.splitColor ||
                "var(--border)"
            );
        },
    }
}