<template>
    <div class="tag-page">
        <b-tag>this is tag</b-tag>
        <b-tag type="dot">this is dot</b-tag>
        <b-tag closable>tag closable</b-tag>
        <b-tag type="dot" closable>dot closable</b-tag>
        <hr/>
        <h3>size:default mediun large</h3>
        <b-tag></b-tag>
        <b-tag size="medium"></b-tag>
        <b-tag size="large"></b-tag>
        <b-tag size="medium" type="dot"></b-tag>
        <b-tag size="large" type="dot"></b-tag>
        <b-tag size="medium" type="dot" closable></b-tag>
        <b-tag size="large" type="dot" closable></b-tag>
        <b-tag size="large" type="border" closable></b-tag>
        <hr/>
        <h3>tag color</h3>
        <b-tag></b-tag>
        <b-tag  color="info">info</b-tag>
        <b-tag  color="success">success</b-tag>
        <b-tag  color="warning">warning</b-tag>
        <b-tag  color="error">error</b-tag>
        <b-tag  closable color="joker">joker</b-tag>
        <b-tag  closable color="primary"></b-tag>
        <hr>
        <b-tag plain></b-tag>
        <b-tag plain  color="info">info</b-tag>
        <b-tag plain  color="success">success</b-tag>
        <b-tag plain  color="warning">warning</b-tag>
        <b-tag plain  color="error">error</b-tag>
        <b-tag plain  closable color="joker">joker</b-tag>
        <b-tag plain  closable color="primary"></b-tag>
        <hr>
        <b-tag type="dot"></b-tag>
        <b-tag type="dot"  color="info">info</b-tag>
        <b-tag type="dot"  color="success">success</b-tag>
        <b-tag type="dot"  color="warning">warning</b-tag>
        <b-tag type="dot"  color="error">error</b-tag>
        <b-tag type="dot"  closable color="joker">joker</b-tag>
        <b-tag type="dot"  closable color="primary"></b-tag>
        <hr>
        <h3></h3>
        <b-tag type="dot" color="yellow">color</b-tag>
        <b-tag type="dot" color="purple">color</b-tag>
        <b-tag type="dot" color="#ffa2d3">color</b-tag>

        <b-tag color="yellow">color</b-tag>
        <b-tag color="purple">color</b-tag>
        <b-tag color="#ffa2d3">color</b-tag>

        <b-tag color="yellow" plain>color</b-tag>
        <b-tag color="purple" plain>color</b-tag>
        <b-tag color="#ffa2d3" plain>color</b-tag>
    </div>
</template>
<script>
export default {
    name:'TagPage',
    props:{
        validator(value){
            return oneOf(value,['border','dot'])
        },
        default:'border'
    }
}
</script>
<style scoped>
.tag-page{
    padding: 10px 0px;
}
hr{
    margin: 15px 0px;
}
</style>